import React, { useEffect, useState } from "react";
import {
  Row,
  Col,
  Card,
  Form,
  DatePicker,
  Button,
  Radio,
  Select,
  Empty,
} from "antd";
import { Table, Space } from "antd";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import EmptyImage from "../../../../assets/images/app/empty-list.svg";
import {
  InputChangeValue,
  clearData,
  getStockList,
  setStockParams,
} from "../../../../store/sideMenu/stock/stockSlice";
import {
  getMiniCounters,
  getMiniZones,
  getMiniDivisions,
  getMiniClusters,
  getMiniModels,
  getMiniBrands,
  getMiniBrandFamily,
  getMiniVersion,
  getMiniVehicletypes,
  getMiniZonesClearData,
  getMiniClustersClearData,
  getMiniDivisionsClearData,
  getMiniCountersClearData,
  getMiniBrandsClearData,
  getMiniBrandFamilyClearData,
  getMiniModelsClearData,
  getMiniVersionClearData
} from "../../../../store/mini/miniSlice";
import moment from "moment";
function Filter(props) {
  const [form] = Form.useForm();
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";

  useEffect(() => {
    props.clearData();
    props.getMiniZones();
    props.getMiniCounters();
    props.getMiniDivisions();
    props.getMiniClusters();
    props.getMiniModels();
    props.getMiniBrands();
    props.getMiniVehicletypes();
    props.getMiniVersion();
    props.getMiniBrandFamily();
  }, []);

  const availabilityList = [
    { value: true, name: "Yes" },
    { value: false, name: "No" },
  ];

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const onFinish = () => {
    props.getStockList({
      ...props.stockParams,
      page: 1,
      vehicle__year: props.stockParams.vehicle__year
        ? moment(props.stockParams.vehicle__year).format("YYYY")
        : "",
    });
  };

  function clearFilter() {
    props.setStockParams({
      page: 1,
      search: "",
      page_size: 10,
    });
    props.getStockList({
      page: 1,
      search: "",
      page_size: 10,
    });
    props.getMiniZones();
    props.getMiniCounters();
    props.getMiniDivisions();
    props.getMiniClusters();
    props.getMiniModels();
    props.getMiniVehicletypes();
    props.getMiniBrandFamily();
    props.getMiniVersion();
    props.getMiniBrands();
  }

  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck = current && current < moment(startDate, "YYYY");
      }
      if (endDate) {
        endCheck = current && current > moment(endDate, "YYYY");
      }
      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  console.log("props.miniModelsList", props.miniModelsList);

  useEffect(() => {
    form.setFieldsValue({
      counter__zone: props.stockParams?.counter__zone,
      counter__division: props.stockParams?.counter__division,
      counter__cluster: props.stockParams?.counter__cluster,
      counter: props.stockParams.counter,
      vehicle__year: props.stockParams.vehicle__year,
      vehicle__version__model:
        props.stockParams.vehicle__version__model,
      vehicle__version__brand: props.stockParams.vehicle__version__brand,
      vehicle__version__brandfamily:
        props.stockParams.vehicle__version__brandfamily,
      vehicle__version__vehicletype:
        props.stockParams.vehicle__version__vehicletype,
      vehicle__version: props.stockParams.vehicle__version,
      availability: props.stockParams?.availability,
    });
  }, [props.stockParams]);
  return (
    <Row gutter={[12, 12]}>
      <Col span={24}>
        <Card>
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col
                span={24}
                xs={24}
                md={6}
                xl={6}
                className="hp-pr-sm-0 hp-pr-12"
              >
                <Form.Item label="Zone Name:" name="counter__zone" className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select  Zone"
                    optionFilterProp="zone"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.getMiniDivisions({ zone: value });
                      props.getMiniClustersClearData();
                      props.getMiniCountersClearData();
                      props.InputChangeValue({
                        key: "counter__zone",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.zone_id == null
                        ? ""
                        : props.stockParams?.zone_id
                    }
                  >
                    {props.miniZonesList.map((zone, index) => {
                      return (
                        <Option key={index} value={zone.id}>
                          {zone.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={6} xl={6}>
                <Form.Item label="Division Name:" name="counter__division" className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select  Division"
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.getMiniClusters({ division: value });
                      props.getMiniClustersClearData();
                      props.getMiniCountersClearData();
                      props.InputChangeValue({
                        key: "counter__division",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.division_id == null
                        ? ""
                        : props.stockParams?.division_id
                    }
                  >
                    {props.miniDivisionsList.map((division, index) => {
                      return (
                        <Option key={index} value={division.id}>
                          {division.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={6} xl={6}>
                <Form.Item label="Cluster Name:" name="counter__cluster" className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select Cluster"
                    optionFilterProp="cluster"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.getMiniCounters({ cluster: value });
                      props.getMiniCountersClearData();
                      props.InputChangeValue({
                        key: "counter__cluster",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.cluster_id == null
                        ? ""
                        : props.stockParams?.cluster_id
                    }
                  >
                    {props.miniClustersList.map((cluster, index) => {
                      return (
                        <Option key={index} value={cluster.id}>
                          {cluster.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={6} xl={6}>
                <Form.Item label="Counter :" name="counter" className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select a Counter"
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.InputChangeValue({
                        key: "counter",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.counter_id == null
                        ? ""
                        : props.stockParams?.counter_id
                    }
                  >
                    {props.miniCountersList.map((bank, index) => {
                      return (
                        <Option key={index} value={bank.id}>
                          {bank.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={6} xl={6}>
                <Form.Item label="Brand Name:" name="vehicle__version__brand" className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select Brand"
                    optionFilterProp="brand"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.getMiniBrandFamily({ brand: value });
                      props.getMiniBrandFamilyClearData();
                      props.getMiniModelsClearData();
                      props.getMiniVersionClearData();
                      props.InputChangeValue({
                        key: "vehicle__version__brand",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.brand_id == null
                        ? ""
                        : props.stockParams?.brand_id
                    }
                  >
                    {props.miniBrandsList.map((brand, index) => {
                      return (
                        <Option key={index} value={brand.id}>
                          {brand.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={6} xl={6}>
                <Form.Item
                  label="BrandFamily Name:"
                  name="vehicle__version__brandfamily"
                  className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select BrandFamily"
                    optionFilterProp="brandfamily"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.getMiniModels({ brandfamily: value });
                      props.getMiniModelsClearData();
                      props.getMiniVersionClearData();
                      props.InputChangeValue({
                        key: "vehicle__version__brandfamily",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.brandfamily_id == null
                        ? ""
                        : props.stockParams?.brandfamily_id
                    }
                  >
                    {props.miniBrandFamilyList.map((brandfamily, index) => {
                      return (
                        <Option key={index} value={brandfamily.id}>
                          {brandfamily.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col
                span={24}
                xs={24}
                md={6}
                xl={6}
                className="hp-pr-sm-0 hp-pr-12"
              >
                <Form.Item label="Model :" name="vehicle__version__model" className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Model"
                    optionFilterProp="Model"
                    onChange={(value) => {
                      props.getMiniVersion({ model: value });
                      props.getMiniVersionClearData(),
                      props.InputChangeValue({
                        key: "vehicle__version__model",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.model_id == null
                        ? ""
                        : props.stockParams?.model_id
                    }
                  >
                    {props.miniModelsList.map((model, index) => {
                      return (
                        <Option key={index} value={model.id}>
                          {model.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={6} xl={6}>
                <Form.Item label="Version :" name="vehicle__version" className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Version"
                    optionFilterProp="Version"
                    onChange={(value) => {
                      // props.getMiniVehicletypes({ vehicletype: value });
                      props.InputChangeValue({
                        key: "vehicle__version",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.version_id == null
                        ? ""
                        : props.stockParams?.version_id
                    }
                  >
                    {props.miniVersionList.map((version, index) => {
                      return (
                        <Option key={index} value={version.id}>
                          {version.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={6} xl={6}>
                <Form.Item
                  label="Vehicle Type :"
                  name="vehicle__version__vehicletype"
                  className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    placeholder="Select Vehicle Type"
                    optionFilterProp="vehicletype"
                    onChange={(value) => {
                      props.InputChangeValue({
                        key: "vehicle__version__vehicletype",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.vehicletype_id == null
                        ? ""
                        : props.stockParams?.vehicletype_id
                    }
                  >
                    {props.miniVehicletypesList.map((vehicletype, index) => {
                      return (
                        <Option key={index} value={vehicletype.id}>
                          {vehicletype.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>

              <Col span={24} xs={24} md={6} xl={6}>
                <Form.Item label="Availability" name="availability" className="hp-mb-0">
                  <Select
                    showSearch
                    allowClear
                    placeholder="Select Availability"
                    optionFilterProp="availability"
                    filterOption={(input, option) =>
                      option.children
                        .toLowerCase()
                        .includes(input.toLowerCase())
                    }
                    onChange={(value) => {
                      props.InputChangeValue({
                        key: "availability",
                        value: value,
                      });
                    }}
                    value={
                      props.stockParams.availability == null
                        ? ""
                        : props.stockParams?.availability
                    }
                  >
                    {availabilityList.map((availability, index) => {
                      return (
                        <Option key={index} value={availability.value}>
                          {availability.name}
                        </Option>
                      );
                    })}
                  </Select>
                </Form.Item>
              </Col>
              <Col span={24} xs={24} md={6} xl={6}>
                <Form.Item label="Year" name="vehicle__year" className="hp-mb-0">
                  <DatePicker
                    id="year"
                    name="vehicle__year"
                    style={{ width: "100%" }}
                    picker="year"
                    format="YYYY"
                    disabledDate={disableDateRanges({
                      endDate: new Date(),
                      startDate: 1990,
                    })}
                    placeholder="Select Year"
                    value={props.stockParams?.year}
                    onChange={(date) => {
                      props.InputChangeValue({
                        key: "vehicle__year",
                        value: date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>

             

              <Col md={4} className="hp-mt-18">
                <Button
                  block
                  type="secondary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    clearFilter();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
              </Col>

              <Col md={4} className="hp-mt-18">
                <Button
                  block
                  type="primary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Apply
                </Button>
              </Col>
            </Row>
            {/* <Button
              type="primary"
              className="hp-submit-btn hp-float-right hp-mt-10"
              disabled={props.loading}
              onClick={() => {
                form.submit();
              }}
            >
              Apply
            </Button> */}
          </Form>
        </Card>
      </Col>
    </Row>
  );
}

function mapStateToProps({ stock, mini }) {
  return {
    loading: stock.loading,
    stockParams: stock.stockParams,
    miniZonesList: mini.miniZonesList,
    miniCountersList: mini.miniCountersList,
    miniDivisionsList: mini.miniDivisionsList,
    miniClustersList: mini.miniClustersList,
    miniModelsList: mini.miniModelsList,
    miniBrandsList: mini.miniBrandsList,
    miniBrandFamilyList: mini.miniBrandFamilyList,
    miniVersionList: mini.miniVersionList,
    miniVehicletypesList: mini.miniVehicletypesList,
  };
}

export default connect(mapStateToProps, {
  InputChangeValue,
  clearData,
  getMiniCounters,
  getMiniZones,
  getMiniDivisions,
  getMiniClusters,
  getMiniModels,
  getStockList,
  setStockParams,
  getMiniBrands,
  getMiniBrandFamily,
  getMiniVersion,
  getMiniVehicletypes,
  getMiniZonesClearData,
  getMiniClustersClearData,
  getMiniDivisionsClearData,
  getMiniCountersClearData,
  getMiniBrandsClearData,
  getMiniBrandFamilyClearData,
  getMiniModelsClearData,
  getMiniVersionClearData
})(Filter);
