import React from "react";

import { Card, Row, Col, Table, Space, Spin } from "antd";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

function StockList(props) {
  const { stockList,  } = props;
  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";

  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "3%",
    },
    {
      title: "Date",
      dataIndex: "date",
      width: "10%",
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Doc No",
      dataIndex: "code",
      width: "10%",
      render: (text) => <span className={columnClass}>{text}</span>,
    },
    {
      title: "Customer",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>{row.customer?.firstname}</span>
      ),
    },
    {
      title: "Vehicle No",
      width: "10%",
      render: (row) => (
        <Link to={`/pages/purchases-view/` + row.id}>
          {row.vehicle.vehicleno}
        </Link>
      ),
    },
    {
      title: "Counter Name",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.counter?.name}</span>,
    },
    {
      title: "Party Type",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.partytype_name}</span>,
    },
    {
      title: "Version",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.vehicle?.version?.name}</span>,
    },
    {
      title: "Brand",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.vehicle?.version?.brand?.name}</span>,
    },
    {
      title: "Brand Family",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.vehicle?.version?.brandfamily?.name}</span>,
    },
    {
      title: "Status",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.approvalstatus_name}</span>,
    },
    // {
    //   title: "Actions",
    //   key: "action",
    //   sorter: true,
    //   width: "10%",
    //   render: (row, record) => (
    //     <Space size="middle">
    //       <Link to={`/pages/purchasesadd/` + row.id}>
    //         <i
    //           className="ri-edit-box-line hp-mr-2 "
    //           style={{ cursor: "pointer", color: "#fc6f03" }}
    //         ></i>
    //       </Link>
    //       <i
    //         className="ri-delete-bin-2-line "
    //         style={{ cursor: "pointer", color: "#ff0000" }}
    //         onClick={() => deletePurchase(row)}
    //       ></i>
    //     </Space>
    //   ),
    // },
  ];
  const data = stockList.map((row, key) => ({
    ...row,
    // index: key + 1,
    index: (props.stockParams.page - 1) * props.stockParams.page_size + (key + 1),
  }));

  return (
    <Card>
      <Row className="hp-mb-12">
        <Col span={24}>
          <Spin tip="Loading..." spinning={props.loading} delay={500}>
            <Table
              columns={columns}
              rowClassName="hp-btn-bg-color-none"
              dataSource={data}
              scroll={{x: 1300}}
              pagination={false}
              size="middle"
            />
          </Spin>
        </Col>
      </Row>
    </Card>
  );
}


function mapStateToProps({ stock }) {
  return {
    loading: stock.loading,
    stockParams: stock.stockParams,
  };
}

export default connect(mapStateToProps, {

})(StockList);