import React, { useEffect, useState } from "react";
import {
  Row,
  Col,
  Card,
  Form,
  DatePicker,
  Empty,
  Button,
  Input,
  Pagination,
  Drawer,
  Spin,
  List,
  Popconfirm,
} from "antd";
import {
  FilterOutlined,
  UploadOutlined,
  PlusOutlined,
  OrderedListOutlined,
  AppstoreOutlined,
} from "@ant-design/icons";
import { connect } from "react-redux";
import moment from "moment";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import PageTitle from "../../../../layout/components/content/page-title";
import EmptyImage from "../../../../assets/images/app/empty-list.svg";
import {
  InputChangeValue,
  clearData,
  getStockList,
  setStockParams,
  clearStockList,
} from "../../../../store/sideMenu/stock/stockSlice";
import StockGrid from "./stockGrid";
import StockList from "./stockList";
import Filter from "./filter";
import { getReportList } from "../../../../store/reports/reportsSlice";
import { RiErrorWarningLine } from "react-icons/ri";

function Stock(props) {
  const [form] = Form.useForm();
  const { Search } = Input;
  const [productListToggle, setProductListToggle] = useState(false);
  const [isShown, setIsShown] = useState(false);
  const columnClass =
    "hp-p1-body hp-text-color-black-50 hp-text-color-dark-0 hp-font-weight-500 ";

  useEffect(() => {
    props.clearData();
    props.getStockList({
      ...props.stockParams,
      page: 1,
      search: "",
      page_size: 10,
    });
  }, []);

  const handleClick = () => {
    //  toggle shown state
    setIsShown((current) => !current);

    //  or simply set it to true
    // setIsShown(true);
  };

  function ChangeHandeler(e) {
    props.InputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }
  const onFinish = () => {};
  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  function onSearch(val) {
    props.getStockList({ ...props.stockParams, page: 1, search: val });
    props.setStockParams({ ...props.stockParams, page: 1, search: val });
  }

  function onPageChange(page, pageSize) {
    props.clearStockList();
    props.setStockParams({
      ...props.stockParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getStockList({
      ...props.stockParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.stockParams?.start_date,
      end_date: props.stockParams?.end_date,
    });
  }, [props.stockParams]);

  function exportData() {
    // var { file_format, ...params } = props.reportParamsData;
    var objData = {
      file_format: 0,
      model_name: "Stock",
    };

    props.getReportList({
      objData,
      ParamsData: {
        ...props.stockParams,
        start_date:
          props.stockParams.start_date != null &&
          props.stockParams.start_date != ""
            ? moment(props.stockParams.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          props.stockParams.end_date != null && props.stockParams.end_date != ""
            ? moment(props.stockParams.end_date)
                .add(1, "days")
                .format("YYYY-MM-DD")
            : "",
      },
    });
  }

  return (
    <Row gutter={[12, 12]}>
      <PageTitle pageTitle="Stock" goBack={false} />
      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          {/* <Col md={12} xs={24} className="hp-ecommerce-app-header-search "> */}
          <Col md={18} xs={24} className="hp-ecommerce-app-header-search ">
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getStockList({
                    ...props.stockParams,
                    page: 1,
                    search: "",
                  });
                  props.setStockParams({
                    ...props.stockParams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>

          <Col md={4} xs={8}>
            <Button
              // onClick={() => {
              //   props.isDrawerVisible(true);
              // }}
              onClick={handleClick}
              className="hp-w-100 hp-p-12 "
              icon={<FilterOutlined className="hp-mr-10" />}
              size="small"
            >
              {" "}
              Filter
            </Button>
          </Col>

          <Col md={1} xs={4}>
            <Button
              className="hp-w-100 hp-p-12"
              icon={<AppstoreOutlined />}
              onClick={() => setProductListToggle(false)}
            ></Button>
          </Col>

          <Col md={1} xs={4}>
            <Button
              className="hp-w-100 hp-p-12"
              icon={<OrderedListOutlined />}
              onClick={() => setProductListToggle(true)}
            ></Button>
          </Col>
        </Row>
      </Col>

      {isShown && (
        <Col span={24} xs={24}>
          <Filter />
        </Col>
      )}

      <Col
        span={24}
        xs={24}
        className="hp-purchase-app-content"
        productListToggle={productListToggle}
        setProductListToggle={setProductListToggle}
      >
        <Spin tip="Loading..." spinning={props.loading} delay={500}>
          {props.stockList.length !== 0 ? (
            <>
              {!productListToggle ? (
                <>
                  <List
                    dataSource={props.stockList}
                    renderItem={(value) => <StockGrid value={value} />}
                  />
                  <Pagination
                    size="small"
                    current={props.stockParams?.page}
                    pageSize={props.stockParams?.page_size}
                    total={props.listCount}
                    showTotal={showTotal}
                    showSizeChanger
                    onChange={onPageChange}
                    className="hp-mb-12 hp-mt-24 hp-float-right"
                  />
                </>
              ) : (
                <>
                  <StockList
                    stockList={props.stockList}
                    // deletePurchase={deleteRecord}
                  />{" "}
                  <Pagination
                    size="small"
                    current={props.stockParams?.page}
                    pageSize={props.stockParams?.page_size}
                    total={props.listCount}
                    showTotal={showTotal}
                    showSizeChanger
                    onChange={onPageChange}
                    className="hp-mb-12 hp-mt-24 hp-float-right"
                  />
                </>
              )}
            </>
          ) : (
            <Card>
              <Empty
                className="hp-m-32"
                image={EmptyImage}
                imageStyle={{
                  height: 160,
                }}
                description={<h5>Your Stock list is empty</h5>}
              >
                {/* <Button type="primary">
                <Link to=" ">Go to Purchase list</Link>
              </Button> */}
              </Empty>
            </Card>
          )}
        </Spin>
      </Col>
    </Row>
  );
}

function mapStateToProps({ stock, system }) {
  return {
    loading: stock.loading,
    stockParams: stock.stockParams,
    stockList: stock.stockList,
    listCount: stock.listCount,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  InputChangeValue,
  clearData,
  getStockList,
  setStockParams,
  getReportList,
  clearStockList,
})(Stock);
